import VueRouter from 'vue-router'
import Vue from 'vue'
import Manage from "../components/Manage"
import Home from '../components/Home'
import Login from '../components/Login'
import Signup from '../components/Signup'
import Test from '../components/Test'
import Mychat from '../components/Mychat'
import Mymain from "../components/Chat/Mymain"

import Sup from "../components/SL/Sup"
import Sup1 from "../components/SL/SL_C/Sup1"
import Sup2 from "../components/SL/SL_C/Sup2"
import Sup3 from "../components/SL/SL_C/Sup3"

import Slbt from "../components/Slbt"
import store from '../store'

Vue.use(VueRouter)

const routes = [
  {
    path:"/",
    name:"home",
    component:Home,
    meta:{needLogin:false},
  },
  {
    path:"/admin",
    name:"admin",
    component:Manage,
  },
  {
    path:"/manage",
    name:"manage",
    component:Test,
    meta:{needLogin:true},
  },
  {
    path:"/mymain",
    name:"mymain",
    component:Mymain,
  },
  {
    path:"/mychat",
    name:"mychat",
    component:Mychat,
    meta:{needLogin:true},
    children:[
      {
        path:"/",
        name:"slbt",
        component:Slbt
      },
      {
        path:"signup",
        name:"sg",
        component:Sup,
        children:[
          {
            path:"/",
            name:"sg1",
            component:Sup1,
          },
          {
            path:"signup2",
            name:"sg2",
            component:Sup2,
          },
          {
            path:"signup3",
            name:"sg3",
            component:Sup3,
          },
        ]
      }
    ]
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to,from,next)=>{
  // console.log(to)
  if((to.name!=='signup' || to.name!=='login') && (to.meta.needLogin) ){
    console.log('对不起您需要登录...')
    if(store.state.isLogined){
      next()
    }else{
      next('/login')
    }
  }else{
    next()
  }
})

router

export default router
